@import '../../utils/variables';
.header {
  position: relative;
  width: 100%;
  background-color: white;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    .logoWrap {
      position: relative;
      display: flex;
      align-items: center;
      // background-color: green;
      margin-right: 20px;
      .logoImg {
        width: 60px;
        margin-right: 10px;
      }
      .logoText {
        font-size: 18px;
        font-weight: 900;
        color: #28262d;
      }
    }
    .menuWrap {
      position: relative;
      display: flex;
      align-items: center;
      flex-grow: 1;
      justify-content: space-between;
    }
    .menuIcon {
      position: relative;
      display: none;
    }
    .logout {
      color: $color-primary;
      border: 1px solid $color-primary;
    }
    // .connect{
    //   display: none;
    // }
  }

  @media (max-width: 768px) {
    background-color: $color-primary;
    .container {
      .logoWrap {
        .logoText {
          color: white;
        }
      }
      .button {
        margin: 5px 0;
        width: 170px;
      }
      .menuWrap {
        display: none;
        flex-direction: column;
        position: absolute;
        right: 0;
        top: 80px;
        z-index: 1000;
        background-color: white;
        padding: 10px;
        border-radius: 8px;
        .navWrap {
          display: flex;
          flex-direction: column;
        }
        &.show {
          display: flex;
        }
      }
      .menuIcon {
        position: relative;
        display: contents;
      }
    }
  }
}
