@import '../../utils/variables';
.calendar{

  .calendarHeader{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    .icon{
      display: flex;
      justify-content: center;
      background-color: $color-primary;
      margin: 0 20px;
      padding: 8px;
      border-radius: 5px;
      box-shadow: 0 3px 23px 0 #D7E0FF;
      cursor: pointer;
      .anticon{
        color: white;
        font-weight: bold;
      }
    }

    .monthName{
      width: 100px;
      font-size: 20px;
      font-weight: 600;
    }
  }
  .contentWrapper{
    margin-top: 20px;
    display: flex;
    gap: 20px;
  }
  .monthList{
    background: white;
    border-radius: 10px;
    padding: 30px 20px;
    user-select: none;
    .monthButton{
      text-transform: capitalize;
      font-size: 15px;
      color: #ABACB1;
      margin: 5px 0;
      width: 100px;
      padding: 8px 0;
      border-radius: 5px;
      cursor: pointer;
      font-weight: 500;
      &.active{
        background: #2659FD;
        box-shadow: 0 3px 23px 0 #D7E0FF;
        color: white;
      }
    }
  }

  @media(max-width: 765px){
    .calendarHeader{
      .monthName{
        width: 80px;
        font-size: 18px;
      }
    }
    .contentWrapper{
      margin-top: 20px;
      display: flex;
      gap: 0;
    }
    .monthList {
      display: none;
    }
  }

}