@import '../../utils/variables';
.banner {
  position: relative;
  width: 100%;
  background-color: $color-primary;
  height: 140px;
  .container {
    position: relative;
    z-index: 2;
    text-align: left;
    color: white;
    h2 {
      margin-top: 0;
      color: white;
    }
    top: 50%;
    transform: translateY(-50%);

    .bannerTitle{
      font-size: 18px;
      font-weight: 800;
    }
    .bannerText{
      font-size: 15px;
      margin: 0;
      line-height: 20px;
      //font-weight: bold;
    }

    .bannerImg{
      z-index: 1;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      height: 120px;
    }
  }


  @media(max-width: 765px){
    .bannerImg{
      opacity: 0.5;
    }
  }
}
