@import '../../utils/variables';
.about {
  position: relative;
  width: 100%;
  .container {
    text-align: justify;
    .blockTitleWrap {
      display: flex;
      align-items: center;
      margin-top: 20px;
      .bullet {
        width: 4px;
        height: 16px;
        background-color: $color-primary;
        margin-right: 10px;
      }
      .blockTitle {
        font-size: 20px;
        font-weight: 600;
      }
    }

    .blockContent {
      font-size: 14px;
      color: #666666;
      margin: 0 0 10px 0;
    }
  }
}
