@import '../../utils/variables';
.date {
  position: relative;
  text-align: left;
  background: #ffffff;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 10px;
  width: calc(14.28% - 10px);
  height: 100px;
  cursor: pointer;

  &:hover {
    border-color: $color-primary !important;
  }
  .borderTop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    border: 1px solid #e1e1e1;
    &.active {
      border: none;
    }
  }
  .day,
  .price,
  .name {
    user-select: none;
  }

  .day {
    color: #28262d;
    font-size: 20px;
    font-weight: bold;
  }

  .price {
    color: #3a68ff;
    font-size: 16px;
    font-weight: 600;
    //margin: 10px 0;
    display: flex;
    align-items: center;
    img {
      width: 15px;
      margin-left: 5px;
    }
  }

  .name {
    color: #666666;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &.active {
    background-color: $color-primary;
    border: none;
    .day,
    .price,
    .name {
      color: white !important;
    }
  }

  @media (max-width: 765px) {
    width: calc(20% - 5px);
    height: auto;
    .day {
      font-size: 14px;
      font-weight: 600;
    }

    .price {
      color: #3a68ff;
      font-size: 13px;
      font-weight: 500;
      margin-bottom: 0;
    }

    .name {
      display: none;
    }
  }
}
