@import '~antd/dist/antd.css';
@import './utils/variables';

.App {
  position: relative;
  text-align: center;
  background-color: #f0f4ff;
  width: 100vw;
  min-height: 100vh;

  a {
    text-decoration: none;
  }

  .button {
    position: relative;
    min-width: 120px;
    border-radius: 8px;
    padding: 10px;
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
    user-select: none;
    color: $color-default-font;

    &:hover {
      opacity: 0.9;
    }
    &.primary,
    &.selected {
      background-color: $color-primary;
      color: white;
    }
    &.active {
      background-color: $color-button-active;
      color: $color-primary;
    }
  }

  .container {
    position: relative;
    max-width: 1200px;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
  h1,
  h2,
  h3,
  p {
    margin: 10px 0;
  }
}
