@import '../../utils/variables';
.dayDetail{
  z-index: 10;
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  padding: 30px 25px;
  background: white;
  width: 300px;
  text-align: left;
  box-shadow: 0 2px 33px 0 rgba(183,181,181,0.5);
  transition: all 0.3s ease;
  overflow-y: scroll;
  display: flex;
  align-items: flex-start;
  overflow-x: hidden;
  flex-direction: column;
  justify-content: center;
  //transform: translateX(300px);

  hr{
    width: 100%;
    background-color: #E1E1E1;
    margin-bottom: 20px;
  }
  .closeButton{
    font-size: 25px;
    color: $color-primary;
    font-weight: bold;
  }
  .monthDay{
    width: 100%;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 30px;
    color: #1A1C1C;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .buyDay, .ownedDay{
    .subtitle{
      font-size: 16px;
      color: #28262D;
      font-weight: 500;
      margin: 30px 0 15px 0;
      text-transform: uppercase;
    }

    .colorSelector{
      flex: 0 0 28px;
      width: 28px;
      height: 28px;
      border: 2px solid #FFFFFF;
      border-radius: 50%;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      &.active{
        transform: scale(1.2);
      }
    }

    .priceWrap{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin: 30px 0 0 0;
      .subtitle{
        margin: 0 15px 0 0;
      }

      .price{
        font-size: 25px;
        font-weight: 600;
        color: $color-blue;
        display: flex;
        align-items: center;
        img{
          width: 25px;
          margin-left: 5px;
        }
      }
    }

    .button{
      margin-top: 50px;
    }
  }

  .buyDay{
    position: relative;
    .nameInput{
      width: calc(100%);
      border: 1px solid #E1E1E1;
      height: 40px;
      font-size: 16px;
      padding: 0 10px;
      border-radius: 5px;
      color: #999999;
    }

    .colorSelectorWrapper{
      position: relative;
      display: flex;
      gap: 15px;
      flex-wrap: wrap;
    }

    .textInput{
      width: calc(100%);
      border: 1px solid #E1E1E1;
      color: #999999;
    }
  }

  .ownedDay{
    .name{
      font-size: 24px;
      color: #1A1C1C;
      font-weight: 500;
    }
    .owner{
      color:$color-default-font;
      font-size: 18px;
      font-weight: 500;
    }

    .remark{
      color: $color-grey;
      font-size: 15px;
    }
  }

  @media(max-width: 765px){
    z-index: 2;
    height: calc(100vh - 60px);
    top: 0;
    .closeButton{
      font-size: 20px;
    }

    .monthDay{
      font-size: 25px;
    }

    .buyDay, .ownedDay{
      .subtitle{
        font-size: 15px;
      }

      .colorSelector{
        flex: 0 0 20px;
        width: 20px;
        height: 20px;
      }

      .priceWrap{
        margin: 30px 0 0 0;
        .subtitle{
          margin: 0 10px 0 0;
        }
        .price{
          font-size: 20px;
          font-weight: 600;
          color: $color-blue;
        }
      }

    }

    .buyDay{
      .nameInput{
        height: 30px;
      }

      .colorSelectorWrapper{
        gap: 10px;
      }
    }

    .ownedDay{
      .name{
        font-size: 20px;
        color: #1A1C1C;
        font-weight: 500;
      }
      .owner{
        color:$color-default-font;
        font-size: 14px;
        font-weight: 500;
      }

      .remark{
        color: $color-grey;
        font-size: 13px;
      }
    }
  }
}