.footer {
  position: relative;
  width: 100%;
  padding: 15px 0;
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    .mediaList {
      position: relative;
      margin: 0 20px;
      a {
        margin: 0 10px;
        img {
          position: relative;
          width: 25px;
        }
      }
    }
    .line {
      width: 100px;
      border: 1px solid #cfd8f2;
    }
  }
  @media(max-width: 765px){
    .container{
      .line{
        width: calc((100% - 60px - 45px * 3) / 2);
      }
    }

  }
}
